import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { PortalSomethingComponent } from './something.component';

@NgModule({
    imports: [
        RouterModule.forChild([
            {
                path: '',
                component: PortalSomethingComponent,
                children: [
                    {
                        path: '',
                        redirectTo: 'list-something',
                        pathMatch: 'full'
                    },
                    {
                        path: 'create-something',
                        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\create-something\\create-something.module')['PortalCreateSomethingModule']);  });}),
                    },
                    {
                        path: 'list-something',
                        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\list-something\\list-something.module')['PortalListSomethingModule']);  });}),
                    },
                    {
                        path: 'list-draft-something',
                        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\list-draft-something\\list-draft-something.module')['PortalListDraftSomethingModule']);  });}),
                    }
                ]
            },
        ])
    ],
    exports: [RouterModule],
    declarations: [
        PortalSomethingComponent
    ]
})

export class PortalSomethingModule { }   


